import React, { useState, useEffect } from "react";
import { NavLink } from "react-router-dom";
import logo from "../logo.png";

const Navbar = ({ setCursorHovered }) => {
  const [isMobile, setIsMobile] = useState(false);
  const [sticky, setSticky] = useState(false);
  function Sticky() {
    if (window.scrollY > 40) {
      setSticky(true);
    } else {
      setSticky(false);
    }
  }

  const goToTop = () => {
    window.scrollTo({ top: 0, behaviour: "smooth" });
  };

  useEffect(() => {
    document.addEventListener("scroll", Sticky);
    return () => {
      document.removeEventListener("scroll", Sticky);
    };
  }, [sticky]);
  return (
    <>
      <nav className={sticky ? "navbar Sticky" : "navbar"}>
        <div className="max-width">
          <div className="logo">
            <NavLink
              to="/"
              onMouseEnter={() => setCursorHovered(true)}
              onMouseLeave={() => setCursorHovered(false)}
              onClick={goToTop}
            >
              {" "}
              <img src={logo} alt={logo} /> Akshit Rana{" "}
            </NavLink>
          </div>
          <ul className={isMobile ? "menu active" : "menu"} id="nav-menu">
            <li>
              {" "}
              <a
                href="#home"
                className="nav-link"
                onClick={() => {
                  setIsMobile(!isMobile);
                }}
                onMouseEnter={() => setCursorHovered(true)}
                onMouseLeave={() => setCursorHovered(false)}
              >
                Home
              </a>{" "}
            </li>
            <li>
              {" "}
              <a
                href="#About"
                className="nav-link"
                onClick={() => {
                  setIsMobile(!isMobile);
                }}
                onMouseEnter={() => setCursorHovered(true)}
                onMouseLeave={() => setCursorHovered(false)}
              >
                About
              </a>
            </li>
            <li>
              {" "}
              <a
                href="#skills"
                className="nav-link"
                onClick={() => {
                  setIsMobile(!isMobile);
                }}
                onMouseEnter={() => setCursorHovered(true)}
                onMouseLeave={() => setCursorHovered(false)}
              >
                Skills
              </a>
            </li>
            <li>
              {" "}
              <a
                href="#projects"
                className="nav-link"
                onClick={() => {
                  setIsMobile(!isMobile);
                }}
                onMouseEnter={() => setCursorHovered(true)}
                onMouseLeave={() => setCursorHovered(false)}
              >
                Projects
              </a>
            </li>
            <li>
              {" "}
              <a
                href="#contact"
                className="nav-link"
                onClick={() => {
                  setIsMobile(!isMobile);
                }}
                onMouseEnter={() => setCursorHovered(true)}
                onMouseLeave={() => setCursorHovered(false)}
              >
                Contact{" "}
              </a>
            </li>
            <li>
              {" "}
              <a
                href="https://drive.google.com/file/d/18K0x-xml-c09Omvgt5MH1Vun9VoyvAqG/view?usp=sharing"
                className="nav-link"
                target="_open"
                onClick={() => {
                  setIsMobile(!isMobile);
                }}
                onMouseEnter={() => setCursorHovered(true)}
                onMouseLeave={() => setCursorHovered(false)}
              >
                Resume
              </a>
            </li>
          </ul>
          <div
            id="menu-btn"
            className={isMobile ? "menu-btn active" : "menu-btn"}
            onClick={() => {
              setIsMobile(!isMobile);
            }}
            onMouseEnter={() => setCursorHovered(true)}
            onMouseLeave={() => setCursorHovered(false)}
          >
            <div id="one" className="bar half start"></div>
            <div id="two" className="bar"></div>
            <div id="three" className="bar half end"></div>
          </div>
        </div>
      </nav>
    </>
  );
};

export default Navbar;
