import React, { useState } from "react";
import { motion } from "framer-motion";
import useMousePosition from "../hooks/useMouseposition";

const Projects = ({ title, src, id, href, tech, desc, github }) => {
  //get x and y mouse coordinates
  const { x, y } = useMousePosition();
  const [hoverState, setHoverState] = useState(false);

  return (
    <>
      <motion.li
        href={href}
        target="blank"
        key={id}
        className="link"
        onHoverStart={() => setHoverState(true)}
        onHoverEnd={() => setHoverState(false)}
      >
        <motion.div
          initial={{ opacity: 0 }}
          className="floating-image"
          animate={{
            opacity: hoverState ? 1 : 0,
            display: hoverState ? "block" : "none",
            x: x - 200,
            y: y - 100,
          }}
          transition={{
            ease: "linear",
          }}
        >
          <img src={src} alt={title} />
        </motion.div>

        <div className="header">
          <div className="caption">{title}</div>
          <i className="fas fa-external-link-square-alt"></i>
        </div>
        <div className="tags">
          <p className="desc">{desc}</p>
          <span>
            {" "}
            <span>Tech Stack -</span> {tech}
          </span>
        </div>
        <div className="work_btns">
          <a href={github} target="_blank" rel="noopener noreferrer">
            <i className="fab fa-github" title="GitHub"></i>Github
          </a>
          <a href={href} target="_blank" rel="noopener noreferrer">
            <i className="fas fa-link"></i>Live Demo
          </a>
        </div>
      </motion.li>
    </>
  );
};

export default Projects;
