import React, { useEffect, useState } from "react";
import Navbar from "../Components/Navbar";
import Home from "../Components/Home";
import Footer from "../Components/Footer";
import ScrollBtn from "../Components/ScrollBtn";
import ContactBox from "../Components/ContactBox";
import About from "../Components/About";
import Contact from "../Components/Contact";
import Work from "../Components/Work";
import Panel from "../Components/Panel";
import useMousePosition from "../hooks/useMouseposition";
import { motion } from "framer-motion";

const MainRoutes = () => {
  const [cursorHovered, setCursorHovered] = useState(false);
  const { x, y } = useMousePosition();

  useEffect(() => {
    const menu = document.querySelectorAll(".nav-link");
    const width = window.innerWidth;

    function scrollActive() {
      const scrollY = window.pageYOffset;
      if (scrollY < 1400) {
        menu[0].classList.add("active");
        menu[1].classList.remove("active");
        menu[2].classList.remove("active");
        menu[3].classList.remove("active");
        menu[4].classList.remove("active");
      }
      if (scrollY > 1400 && scrollY < 3400) {
        menu[0].classList.remove("active");
        menu[1].classList.add("active");
        menu[2].classList.remove("active");
        menu[3].classList.remove("active");
        menu[4].classList.remove("active");
      }
      if (scrollY > 3400 && scrollY < 4240) {
        menu[0].classList.remove("active");
        menu[1].classList.remove("active");
        menu[2].classList.add("active");
        menu[3].classList.remove("active");
        menu[4].classList.remove("active");
      }
      if (scrollY > 4240 && scrollY < 6300) {
        menu[0].classList.remove("active");
        menu[1].classList.remove("active");
        menu[2].classList.remove("active");
        menu[3].classList.add("active");
        menu[4].classList.remove("active");
      }
      if (scrollY > 6300 && scrollY < 6856) {
        menu[0].classList.remove("active");
        menu[1].classList.remove("active");
        menu[2].classList.remove("active");
        menu[3].classList.remove("active");
        menu[4].classList.add("active");
      }
    }
    if (width > 990) {
      window.addEventListener("scroll", scrollActive);
    }
  }, []);

  return (
    <>
      <div style={{ overflow: "hidden" }}>
        <motion.div
          animate={{
            x: x - 16,
            y: y - 16,
            scale: cursorHovered ? 2.4 : 1,
            opacity: cursorHovered ? 0.4 : 0,
          }}
          transition={{ duration: 0.1, ease: "linear" }}
          className="cursor"
        ></motion.div>
      </div>
      <Panel />
      <Navbar setCursorHovered={setCursorHovered} />
      <ScrollBtn />
      <ContactBox />
      <Home setCursorHovered={setCursorHovered} />
      <About />
      <Work />
      <Contact setCursorHovered={setCursorHovered} />
      <Footer setCursorHovered={setCursorHovered} />
    </>
  );
};

export default MainRoutes;
